import React, { Component } from 'react';
import './SeasonDisplay.css';
import { FaSun, FaRegSnowflake } from 'react-icons/fa';


const findSeason = (lat, month) => {
     if (month > 2 && month < 9) {
          return lat > 0 ? 'Summer' : 'Winter';
     }
     else {
          return lat > 0 ? 'Winter' : 'Summer';
     }
}

const SeasonDisplay = (props) => {
     const season = findSeason(props.lat, new Date().getMonth());
     const text = season === 'Winter' ? 'Burr, it is chilly!' : 'Lets hit the beach';
     const icon = season === 'Winter' ? <FaRegSnowflake /> : <FaSun />;
     return (
          <div className={`${season}`}>
               <span className='icon-left'>{icon}</span>
               <div className='text'>
                    <h1>{season}</h1>
                    <h1>{text}</h1>
               </div>
               <span className='icon-right'>{icon}</span>
          </div>
     );
}

export default SeasonDisplay;
